import React from 'react'
import './LogInBtn.css'
import { useEffect , useRef } from 'react'
import { Link } from 'react-router-dom'

export default function LogInBtn() {
  let NavBtn = useRef()

  function getLocalStorage() {
    let webAppereance = localStorage.getItem("mode");
    if (webAppereance === "dark") {
        NavBtn.current.classList.add("LogInBtn-container-dark");
    } else {
        NavBtn.current.classList.remove("LogInBtn-container-dark");
    }
  }
  
  useEffect(() => {
    const handleStorageUpdate = () => {
        getLocalStorage();
    };
  
    getLocalStorage();
  
    window.addEventListener("lsUpdated", handleStorageUpdate);
  
    return () => {
        window.removeEventListener("lsUpdated", handleStorageUpdate);
    };
  }, []);  

  return (
    <Link className='LogInBtn-container' to="/signing" ref={NavBtn}>SignUp/SignIn</Link>
  )
}
